import React, { Fragment }        from 'react'
import Head         from '../components/head'
import Layout       from '../layouts/home';
import Accouncement from '../components/anouncement';
import posts        from '../posts';

const Home = () => (
    <Fragment>
        <Head title="Home" />
        <Layout title="Hello, World!" subtitle="...and goodbye." cover="/static/img/cover.jpg">
            {
                posts.map((post, key) => <Accouncement key={key} prefetch={ key < 3 } { ...post } />)
            }
        </Layout>
    </Fragment>
)

export default Home
